<template>
  <div class="brush">
    <h4 class="brush-title">刷</h4>

    <div class="brush-content">
      <div
        class="brush-item"
        v-for="(item, index) in list"
        :key="index"
        :class="{ active: brushIndex === item.value }"
        :style="`margin-top:${item.top}px`"
        @click="selectItem(item.value)"
      >
        {{ item.name }}
      </div>
    </div>

    <div class="brush-content-select">
      <div class="brush-select">
        <div class="brush-select-item" @click="selectAction(1, 0)">+x</div>
        <div class="brush-select-item" @click="selectAction(0, -1)">+y</div>
      </div>
      <div class="brush-select">
        <div class="brush-select-item" @click="selectAction(-1, 0)">-x</div>
        <div class="brush-select-item" @click="selectAction(0, 1)">-y</div>
      </div>
    </div>

    <div class="brush-footer">
      <div
        class="brush-footer-item"
        :class="{ active: showGrid }"
        @click="setValue('showGrid')"
      >
        Grid
      </div>
      <div
        class="brush-footer-item"
        :class="{ active: showLayer }"
        @click="setValue('showLayer')"
      >
        Layer
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [{
        name: '画笔(单)',
        value: 'brush'
      }, {
        name: '画笔(块)',
        value: 'brushs'
      }, {
        name: '橡皮擦(单)',
        value: 'detele',
        top: 10
      }, {
        name: '橡皮擦(块)',
        value: 'deteles'
      }, {
        name: '拖拽移动',
        value: 'move',
        top: 10
      }, {
        name: '选择像素',
        value: 'select'
      }, {
        name: '颜色替换',
        value: 'repalce'
      }]
    }
  },
  computed: {
    brushIndex() {
      return this.$state.brushIndex
    },
    showGrid() {
      return this.$state.showGrid
    },
    showLayer() {
      return this.$state.showLayer
    }
  },
  methods: {
    selectItem(value) {
      if (this.$state.brushIndex === value) return false
      this.$state.selects = []
      this.$state.brushIndex = value
      this.$state.drawIndex += 1
    },
    setValue(k) {
      this.$state[k] = !this.$state[k]
      this.$state.drawIndex += 1
    },
    selectAction(x, y) {
      const layer = this.$state.layer.find(item => this.$state.layerIndex === item.uid)
      const selects = this.$state.selects
      const newSelects = []
      if (layer && layer.show) {
        const _x = x
        const _y = this.$state.size * y
        selects.forEach((item, index) => {
          const indexX = index % this.$state.size
          const indexY = Math.floor(index / this.$state.size)
          let targetIndex = index + _x + _y
          if (!((indexX > 0 && indexX % this.$state.size === 0)) || (indexY === this.$state.gridSize)) {
            newSelects[targetIndex] = item
          }
          delete layer.data[index]
        })
        newSelects.forEach((item, index) => {
          const indexX = index % this.$state.size
          const indexY = Math.floor(index / this.$state.size)
          layer.data[indexX + indexY * this.$state.size] = item
        })
        this.$state.selects = [...newSelects]
        this.$save()
      }
    }
  }
}

</script>

