<template>
  <div class="palette">
    <h4 class="palette-title">颜色板</h4>
    <div class="palette-content">
      <div
        class="palette-content-item"
        v-for="(item, $index) in paletteList"
        :key="$index"
        :class="{ active: index === $index }"
        @click="selectItem($index)"
      >
        <div
          class="palette-content-block"
          :style="`background-color: #${item};`"
        ></div>
      </div>
    </div>
    <div class="palette-input">
      <input class="palette-input-block" type="color" v-model="color" />
      <input type="text" class="palette-input-prix" v-model="color" />
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    index() {
      return this.$state.colorIndex
    },
    paletteList() {
      return this.$state.paletteList
    },
    color: {
      get() {
        return '#' + this.paletteList[this.index]
      },
      set(val) {
        this.paletteList[this.index] = val.substr(1)
        this.$save()
      }
    }
  },
  methods: {
    selectItem(i) {
      this.$state.colorIndex = i
    }
  }
}

</script>

