import Vue from 'vue'

const getPaletteList = function(palette = []) {
  const paletteList = []
  for (let i = 0; i < 160; i++) {
    if (palette[i]) {
      paletteList.push(palette[i])
    } else {
      paletteList.push('4b4b4b')
    }
  }
  return paletteList
}

let saveData = []
let initState = {
  width: 2000,
  height: 2000,
  gridSize: 10,
  size: 200,
  x: -500,
  y: -500,
  colorIndex: 0,
  brushIndex: 'brush',
  layerIndex: '',
  saveIndex: -1,
  drawIndex: 0,
  showGrid: true,
  showLayer: true,
  paletteList: getPaletteList([]),
  layer: [],
  selects: []
}

const state = Vue.observable(initState)
Vue.prototype.$state = state
Vue.prototype.$save = function () {
  const paletteList = JSON.parse(JSON.stringify(state.paletteList))
  const layer = JSON.parse(JSON.stringify(state.layer))
  const selects = JSON.parse(JSON.stringify(state.selects))
  state.saveIndex += 1
  state.drawIndex += 1
  saveData[state.saveIndex] = {
    paletteList,
    selects,
    layer
  }
}
Vue.prototype.$back = function () {
  state.saveIndex -= 1
  state.drawIndex += 1
  const data = saveData[state.saveIndex]
  if (data) {
    state.paletteList = data.paletteList
    state.layer = data.layer
    state.selects = []
  }
  if (state.saveIndex < 0) state.saveIndex = 0
}
Vue.prototype.$forward = function () {
  state.saveIndex += 1
  state.drawIndex += 1
  const data = saveData[state.saveIndex]
  if (data) {
    state.paletteList = data.paletteList
    state.layer = data.layer
    state.selects = []
  }
}
