<template>
  <div class="layer">
    <h4 class="layer-title">图层</h4>
    <div class="layer-view" ref="view">
      <transition-group name="list" tag="p">
        <div
          class="layer-item"
          v-for="(item, $index) in layer"
          :key="item.uid"
          :class="{
            active: layerIndex === item.uid,
          }"
          @click="(e) => selectItem(item.uid, e)"
        >
          <img
            v-if="item.show"
            class="layer-icon"
            src="../images/see.png"
            @click="(e) => showLayer(item, e)"
          />
          <img
            v-else
            class="layer-icon"
            src="../images/unSee.png"
            @click="(e) => showLayer(item, e)"
          />
          <input type="text" class="layer-text" v-model="item.name" />
          <img
            class="layer-icon"
            :class="{disabled: $index === 0}"
            src="../images/up.png"
            @click="(e) => upLayer($index, e)"
          />
          <img
            :class="{disabled: $index === layer.length - 1}"
            class="layer-icon"
            style="transform: rotate(180deg);"
            src="../images/up.png"
            @click="(e) => downLayer($index, e)"
          />
          <img
            class="layer-icon"
            src="../images/delete.png"
            @click="(e) => removeLayer($index, e)"
          />
        </div>
      </transition-group>
    </div>

    <div class="layer-add-item" @click="addLayer">新建图层</div>
  </div>
</template>

<script>
const getUid = function () {
  return 'uxxxxxxxxxxxxxxxxx'.replace(/[xy]/g, c => {
    const r = Math.random() * 16 | 0
    const v = c === 'x' ? r : (r & 0x3 | 0x8)
    return v.toString(16)
  })
}

export default {
  data() {
    return {
      selectIndex: ''
    }
  },
  computed: {
    layerIndex() {
      return this.$state.layerIndex
    },
    layer() {
      return this.$state.layer
    }
  },
  mounted() {
    this.addLayer()
    this.$save()
  },
  methods: {
    selectItem(uid, event) {
      event.stopPropagation()
      this.$state.layerIndex = uid
      this.$state.selects = []
    },
    addLayer() {
      this.$state.layer.push({
        name: '图层新建副本' + this.$state.layer.length,
        show: true,
        data: [],
        uid: getUid()
      })
      this.$state.layerIndex = this.$state.layer[this.$state.layer.length - 1].uid
    },
    removeLayer(index, event) {
      event.stopPropagation()
      if (index === this.$state.layerIndex) {
        this.$state.layerIndex -= 1
      }
      this.$state.layer.splice(index, 1)
      this.$state.drawIndex += 1
    },
    upLayer(index, event) {
      event.stopPropagation()
      if (index === 0) return false
      const temp = this.layer.splice(index - 1, 1)
      this.layer.splice(index, 0, temp[0])
      this.$state.drawIndex += 1
    },
    downLayer(index, event) {
      event.stopPropagation()
      if (index === this.layer.length - 1) return false
      const temp = this.layer.splice(index + 1, 1)
      this.layer.splice(index, 0, temp[0])
      this.$state.drawIndex += 1
    },
    showLayer(item, event) {
      event.stopPropagation()
      item.show = !item.show
      this.$state.drawIndex += 1
    }
  }
}

</script>

