<template>
  <div class="draw">
    <div class="draw-header">
      <span class="draw-header-item" @click="importProject">导入工程</span>
      <span class="draw-header-item" @click="exportProject">导出工程</span>
      <span class="draw-header-item" @click="exportImage">导出图片</span>

      <span class="draw-header-item" style="margin-left: 40px" @click="back">撤回</span>
      <span class="draw-header-item" @click="forward">前进</span>
    </div>
    <div class="draw-content" @mousedown="touchstart">
      <canvas class="draw-canvas" ref="canvas" :style="canvasStyle"></canvas>
    </div>
  </div>
</template>

<script>
const isNull = e => (e === undefined || e === null)

export default {
  mounted() {
    this.$nextTick(() => {
      const wrapper = this.$refs.canvas.parentNode
      this.moveMaxX = this.width - wrapper.clientWidth
      this.moveMaxY = this.height - wrapper.clientHeight
      document.addEventListener('mousemove', this.touchmove)
      document.addEventListener('mouseup', this.touchend)
      this.draw()
    })
  },
  computed: {
    canvasStyle() {
      return `transform: translate(${this.$state.x}px, ${this.$state.y}px)`
    },
    drawIndex() {
      return this.$state.drawIndex
    },
    width() {
      return this.$state.width
    },
    height() {
      return this.$state.height
    },
    gridSize() {
      return this.$state.gridSize
    },
    size() {
      return this.$state.size
    }
  },
  methods: {
    getGridPs(e) {
      const pageX = e.pageX
      const pageY = e.pageY
      const canvas = this.$refs.canvas
      const parentRect = canvas.parentNode.getBoundingClientRect()
      const x = pageX - parentRect.left - this.$state.x
      const y = pageY - parentRect.top - this.$state.y
      return { x, y }
    },
    touchstart(e) {
      // 0左键 1中键 2右键
      if (e.button === 2) return false
      this.isTouch = true
      this.pageX = e.pageX
      this.pageY = e.pageY
      this._x = this.$state.x
      this._y = this.$state.y
      this._selects = JSON.parse(JSON.stringify(this.$state.selects))
      this.mouse = e.button
      if (e.button === 0) {
        const ps = this.getGridPs(e)
        this.indexX = Math.floor(ps.x / this.gridSize)
        this.indexY = Math.floor(ps.y / this.gridSize)
        this.action()
      }
    },
    touchmove(e) {
      if (this.isTouch) {
        this.offsetX = e.pageX - this.pageX
        this.offsetY = e.pageY - this.pageY
        if (this.mouse === 0) {
          const ps = this.getGridPs(e)
          this.targetX = Math.floor(ps.x / this.gridSize)
          this.targetY = Math.floor(ps.y / this.gridSize)
          this.action()
        } else if (this.mouse === 1) {
          this.move()
        }
      }
    },
    touchend(e) {
      this.isTouch = false
      this.indexX = null
      this.indexY = null
      this.targetX = null
      this.targetY = null
      this.offsetX = null
      this.offsetY = null
      this.$state.cacelayer = JSON.parse(JSON.stringify(this.$state.layer))
    },
    draw() {
      const canvas = this.$refs.canvas
      const ctx = canvas.getContext('2d')
      canvas.width = this.width
      canvas.height = this.height
      ctx.clearRect(0, 0, 10000, 10000)
      if (this.$state.showLayer) {
        this.$state.layer.forEach(layer => {
          if (layer.show) {
            layer.data.forEach((item, index) => {
              if (!isNull(item)) {
                const x = index % this.size
                const y = Math.floor(index / this.size)
                ctx.fillStyle = `#${this.$state.paletteList[item]}`
                ctx.fillRect(x * this.gridSize, y * this.gridSize, this.gridSize, this.gridSize)
              }
            })
          }
        })
      }
      if (this.$state.showGrid) {
        const gridCanvas = this.getGrid()
        ctx.drawImage(gridCanvas, 0, 0, this.width, this.height)
      }
      if (this.$state.selects.length) {
        this.$state.selects.forEach((item, index) => {
          if (!isNull(item)) {
            const x = index % this.size
            const y = Math.floor(index / this.size)
            ctx.strokeStyle = '#ffffff'
            ctx.strokeRect(x * this.gridSize, y * this.gridSize, this.gridSize, this.gridSize)
          }
        })
      }
    },
    getGrid() {
      if (!this.canvas) {
        this.canvas = document.createElement('canvas')
        const ctx = this.canvas.getContext('2d')
        this.canvas.width = this.width
        this.canvas.height = this.height
        var stepX = this.gridSize
        var stepY = this.gridSize
        ctx.strokeStyle = '#333'
        ctx.moveTo(0, 0)
        ctx.lineTo(this.width, 0)
        ctx.lineTo(this.width, this.height)
        ctx.lineTo(0, this.height)
        ctx.lineTo(0, 0)
        for (var i = stepX + 0.5; i < this.width; i += stepX) {
          ctx.moveTo(i, 0)
          ctx.lineTo(i, this.height)
          ctx.stroke()
        }
        for (var j = stepY + 0.5; j < this.height; j += stepY) {
          ctx.moveTo(0, j)
          ctx.lineTo(this.width, j)
          ctx.stroke()
        }
      }
      return this.canvas
    },
    action() {
      switch (this.$state.brushIndex) {
        case 'brush':
          this.brush()
          break
        case 'brushs':
          this.brushs()
          break
        case 'move':
          this.move()
          break
        case 'detele':
          this.detele()
          break
        case 'deteles':
          this.deteles()
          break
        case 'select':
          this.selectGrid()
          break
      }
    },
    brush() {
      const layer = this.$state.layer.find(item => this.$state.layerIndex === item.uid)
      if (layer && layer.show) {
        layer.data[this.indexX + this.indexY * this.size] = this.$state.colorIndex
        if (this.targetX && this.targetY) {
          layer.data[this.targetX + this.targetY * this.size] = this.$state.colorIndex
        }
      }
      this.draw()
    },
    brushs() {
      const layer = this.$state.layer.find(item => this.$state.layerIndex === item.uid)
      if (layer && layer.show) {
        layer.data[this.indexX + this.indexY * this.size] = this.$state.colorIndex
        if (this.targetX && this.targetY) {
          const boxIndexs = this.getBlockBoxIndex(this.indexX, this.indexY, this.targetX, this.targetY)
          boxIndexs.forEach(index => {
            layer.data[index] = this.$state.colorIndex
          })
        }
      }
      this.draw()
    },
    move() {
      if (this.offsetX && this.offsetY) {
        this.$state.x = this._x + this.offsetX
        this.$state.y = this._y + this.offsetY
        if (this.$state.x > 0) this.$state.x = 0
        if (this.$state.x < -this.moveMaxX) this.$state.x = -this.moveMaxX
        if (this.$state.y > 0) this.$state.y = 0
        if (this.$state.y < -this.moveMaxY) this.$state.y = -this.moveMaxY
      }
    },
    detele() {
      const layer = this.$state.layer.find(item => this.$state.layerIndex === item.uid)
      if (layer && layer.show) {
        delete layer.data[this.indexX + this.indexY * this.size]
        if (this.targetX && this.targetY) {
          delete layer.data[this.targetX + this.targetY * this.size]
        }
      }
      this.draw()
    },
    deteles() {
      const layer = this.$state.layer.find(item => this.$state.layerIndex === item.uid)
      if (layer && layer.show) {
        layer.data[this.indexX + this.indexY * this.size] = this.$state.colorIndex
        if (this.targetX && this.targetY) {
          const boxIndexs = this.getBlockBoxIndex(this.indexX, this.indexY, this.targetX, this.targetY)
          boxIndexs.forEach(index => {
            delete layer.data[index]
          })
        }
      }
      this.draw()
    },
    selectGrid() {
      const layer = this.$state.layer.find(item => this.$state.layerIndex === item.uid)
      const selects = this.$state.selects
      const _selects = this._selects
      if (layer && layer.show) {
        const startSelectIndex = this.indexX + this.indexY * this.size
        if (!isNull(_selects[startSelectIndex])) {
          delete selects[startSelectIndex]
        } else {
          selects[startSelectIndex] = layer.data[startSelectIndex]
        }
        if (this.targetX && this.targetY) {
          const boxIndexs = this.getBlockBoxIndex(this.indexX, this.indexY, this.targetX, this.targetY)
          boxIndexs.forEach(index => {
            if (!isNull(_selects[index])) {
              delete selects[index]
            } else {
              selects[index] = layer.data[index]
            }
          })
        }
      }
      this.draw()
    },
    getBlockBoxIndex(x, y, fx, fy) {
      const numX = Math.abs(x - fx) + 1
      const numY = Math.abs(y - fy) + 1
      const indexs = []
      const startIndexX = Math.min(x, fx)
      const startIndexY = Math.min(y, fy)
      for (let i = 0; i < numX; i++) {
        for (let j = 0; j < numY; j++) {
          indexs.push((i + startIndexX) + (j + startIndexY) * this.size)
        }
      }
      return indexs
    },
    back() {
      this.$back()
    },
    forward() {
      this.$forward()
    },
    importProject() {
      let input = document.createElement('input')
      input.type = 'file'
      input.onchange = event => {
        let file = event.target.files[0]
        let fileReader = new FileReader()
        fileReader.onload = () => {
          const str = decodeURIComponent(fileReader.result)
          const obj = JSON.parse(str)
          this.projectName = file.name
          this.$state.layer = obj.layer
          this.$state.paletteList = obj.paletteList
          this.$state.layerIndex = obj.layer[0].uid
          this.$state.drawIndex += 1
        }
        fileReader.readAsText(file, 'UTF-8')
      }
      input.click()
    },
    exportProject() {
      var pom = document.createElement('a')
      pom.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(JSON.stringify({
        paletteList: this.$state.paletteList,
        layer: this.$state.layer
      })))
      pom.setAttribute('download', this.projectName || '项目工程.jc')
      pom.click()
    },
    exportImage() {
      const showGrid = this.$state.showGrid
      this.$state.showGrid = false
      this.draw()
      this.$state.showGrid = showGrid
      const img = this.$refs.canvas.toDataURL('image/png', 1.0)
      var pom = document.createElement('a')
      pom.setAttribute('href', URL.createObjectURL(this.base64ToBlob(img)))
      pom.setAttribute('download', '图片.png')
      pom.click()
      this.draw()
    },
    base64ToBlob(code) {
      let parts = code.split(';base64,')
      let contentType = parts[0].split(':')[1]
      let raw = window.atob(parts[1])
      let rawLength = raw.length
      let uInt8Array = new Uint8Array(rawLength)
      for (let i = 0; i < rawLength; ++i) {
        uInt8Array[i] = raw.charCodeAt(i)
      }
      return new Blob([uInt8Array], { type: contentType })
    }
  },
  watch: {
    drawIndex() {
      this.draw()
    }
  }
}

</script>

